import Vue from 'vue'
import App from './App.vue'
import VueRouter from 'vue-router'

import Routes from './route.js'
Vue.config.productionTip = false
Vue.use(VueRouter)
//创建路由
const router = new VueRouter({
	routes:Routes,
	mode:"history"
});
new Vue({
  render: h => h(App),
  router:router
}).$mount('#app')
