<template>
	<!-- 此添加博客功能因非动态数据而无法实现且部分功能无法开放 -->
  <div class="editblog">
    <h2>修改博客</h2>
	<form   v-if="!submmited">
		<label for="">主题</label>
		<input type="text" v-model="blogContent.title">
		<label for="">内容</label>
		<textarea v-model="blogContent.body">
		</textarea>
		<!-- <div id="checkbox">
			<label for="">分类:</label>
			<input type="checkbox" value="Vue" v-model="blogContent.options">Vue
			<input type="checkbox" value="Laravel" v-model="blogContent.options">Laravel
			<input type="checkbox" value="element" v-model="blogContent.options">Element
			<input type="checkbox" value="mint" v-model="blogContent.options">Mint
		</div>
		<label>作者:</label> -->
		<!-- <select v-model="blogContent.auth">
			<option v-for="(auths,index) in authors" :key="index">
				{{auths}}
			</option>
		</select> -->
		<button v-on:click.prevent="post">修改博客</button>
	</form>
	<h3 v-if="submmited">修改博客成功!</h3>
	<div class="preview">
		<h3>博客总览</h3>
		<p>博客标题:{{this.blogContent.title}}</p>
		<p>博客内容:</p>
		<p>{{this.blogContent.body}}</p>
		<!-- <p>博客分类:</p>
		<ul>
			<li v-for="(optioni,index) in blog.options" :key="index">{{optioni}}</li>
		</ul>
		<p>作者:{{this.blog.auth}}</p> -->
		
	</div>
  </div>
</template>

<script>
import axios from 'axios';
export default {
  name: 'editblog',
  data(){
	  return {
		  blogContent:{},
		  id:this.$route.params.id,
		  submmited:false,
	  }
  },
  methods:{
	  fetchDate(){
		axios.get('http://localhost:8081/posts.json')
		.then(data=>{
			this.blogContent = data.data[this.id-1];
		})
	  },
	  post:function(){
		  this.submmited = !this.submmited;
		  axios.put('http://localhost:8081/posts.json'+this.id,{
			  title:this.blogContent.title,
			  body:this.blogContent.body,
		  }).then(function(data){
			  console.log(data);
		  })
	  }
  },
  created(){
	  this.fetchDate();
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
	.editblog *{
		box-sizing: border-box;
	}
	.editblog{
		margin: 20px auto;
		max-width: 600px;
		padding: 20px;
	}
	label{
		display: block;
		margin: 20px 0px 10px;
	}
	input[type="text"],textarea,select{
		display: block;
		width: 100%;
		padding: 0px;
	}
	#checkbox label{
		display: inline-block;
		margin-top: 0px;
	}
	#checkbox input{
		display: inline-block;
		margin-right: 10px;
	}
	button{
		display: block;
		margin: 20px 0;
		background-color: crimson;
		color: #fff;
		border: 0;
		padding: 14px;
		border-radius: 4px;
		font-size: 18px;
	}
	textarea{
		height: 150px;
	}
</style>
