<template>
	<div id="single-blog">
			<h2>{{this.blogContent.title}}</h2>
			<p>{{this.blogContent.body}}</p>
		<router-link v-bind:to="/edit/ + this.id"><button>编辑</button></router-link>
		<!-- 此删除功能因非动态数据而无法删除 -->
		<button @click= "deleteBlog">删除</button>
	</div>
</template>
<script>
import axios from 'axios';
	export default{
		name:"single-blog",
		data(){
			return {
				id:this.$route.params.id,
				blogContent:{}
			}
		},
		created() {
			axios.get("http://localhost:8081/posts.json")
			.then((data)=>{
				this.blogContent = data.data[this.id-1];
			}).catch(err=>{
				console.log("数据请求错误");
			})
		},
		methods:{
			deleteBlog(){
				axios.delete("http://localhost:8081/posts.json/"+this.id)
				.then(respose=>{
					this.$route.push({path:'/'})
				}).catch(err=>{
					console.log('删除失败')
				})
			}
		}
	}
</script>
<style scoed>
	#single-blog{
		max-width: 960px;
		margin: 0 auto;
		padding: 20px;
		background-color: #eee;
		border: 1px dotted #aaa;
	}
	button{
		border: 1px solid #aaa;
		font-size: 13px;
		padding: 5px;
		margin-right:12px;
		position: position;
		left:630px
	}
</style>