<template>
  <div id="showBlog">
   <h2>博客总览</h2>
   <input type="text" v-model="search" placeholder="请搜索">
   <div v-for="(blog,index) in fileBlogs" :key="index"  class="single-blog">
	   <router-link v-bind:to="/blog/+ blog.id"><h2>{{blog.title}}</h2></router-link>
	   <article>
		   {{blog.body|omit}}
	   </article>
   </div>
  </div>
</template>
	
<script>
import axios from 'axios';
export default {
	name:'showBlog',
	data(){
		return {
			blogs:[],
			search:''
		}
	},
	created(){
		axios.get('http://localhost:8081/posts.json')
		.then((data)=>{
			return data.data;
		})
		.then((data)=>{
			var blogsArray = [];
			for(let key in data){
				blogsArray.push(data[key]);
			}
			this.blogs = blogsArray;
		})
		.catch(err=>{console.log(error)})
	},
	filters:{
		"omit":function(value){
			return value.slice(0,100)+"...";
		}
	},
	computed:{
		fileBlogs:function(){
			return this.blogs.filter((blog)=>{
				return blog.title.match(this.search);
			});
		}
	}
}
</script>
<style scoped="scoped">

#showBlog{
	max-width: 800px;
	margin: 20px auto;
}
.single-blog{
	box-sizing: border-box;
	background-color: #eee;
	padding: 20px;
	margin: 20px 0;
	border: 1px dotted #aaa;
}
#showBlog a{
	text-decoration: none;
	color: #444;
}
input[type="text"]{
	padding: 8px;
	width: 100%;
	box-sizing: border-box;
}
</style>
