<template>
	<!-- 此添加博客功能因非动态数据而无法实现 -->
  <div class="hello">
    <h2>我的博客</h2>
	<form   v-if="!submmited">
		<label for="">主题</label>
		<input type="text" v-model="blog.title">
		<label for="">内容</label>
		<textarea v-model="blog.content">
		</textarea>
		<div id="checkbox">
			<label for="">分类:</label>
			<input type="checkbox" value="Vue" v-model="blog.options">Vue
			<input type="checkbox" value="Laravel" v-model="blog.options">Laravel
			<input type="checkbox" value="element" v-model="blog.options">Element
			<input type="checkbox" value="mint" v-model="blog.options">Mint
		</div>
		<label>作者:</label>
		<select v-model="blog.auth">
			<option v-for="(auths,index) in authors" :key="index">
				{{auths}}
			</option>
		</select>
		<button v-on:click.prevent="post">添加博客</button>
	</form>
	<h3 v-if="submmited">添加博客成功!</h3>
	<div class="preview">
		<h3>博客总览</h3>
		<p>博客标题:{{this.blog.title}}</p>
		<p>博客内容:</p>
		<p>{{this.blog.content}}</p>
		<p>博客分类:</p>
		<ul>
			<li v-for="(optioni,index) in blog.options" :key="index">{{optioni}}</li>
		</ul>
		<p>作者:{{this.blog.auth}}</p>
		
	</div>
  </div>
</template>

<script>
import axios from 'axios';
export default {
  name: 'Addblog',
  data(){
	  return {
		  blog:{
			 title:'',
			 content:'',
			 options:[],
			 auth:''
		  },
		  submmited:false,
		  authors:['请选择','何无一','何无','何俊松']
	  }
  },
  methods:{
	  post:function(){
		  this.submmited = !this.submmited;
		  axios.post('http://localhost:8081/posts.json',{
			  title:this.blog.title,
			  body:this.blog.content,
			  userId:1
		  }).then(function(data){
			  console.log(data);
		  })
	  }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
	.hello *{
		box-sizing: border-box;
	}
	.hello{
		margin: 20px auto;
		max-width: 600px;
		padding: 20px;
	}
	label{
		display: block;
		margin: 20px 0px 10px;
	}
	input[type="text"],textarea,select{
		display: block;
		width: 100%;
		padding: 0px;
	}
	#checkbox label{
		display: inline-block;
		margin-top: 0px;
	}
	#checkbox input{
		display: inline-block;
		margin-right: 10px;
	}
	button{
		display: block;
		margin: 20px 0;
		background-color: crimson;
		color: #fff;
		border: 0;
		padding: 14px;
		border-radius: 4px;
		font-size: 18px;
	}
	textarea{
		height: 150px;
	}
</style>
